
@use "sass:math";

:deep(.main) {
    display: flex;
    align-items: center;
    justify-content: center;
}

.stage-container {
    display: flex;
    min-width: calc(v-bind(noAirplanesMessageWidth) * 1px);
    min-height: 300px;
    align-items: center;
    justify-content: center;

    @media not all and (any-pointer: coarse) {
        width: 60vw;
        width: 60svw;
        height: 60vh;
        height: 60svh;
    }

    @media (any-pointer: coarse) {
        width: 90vw;
        width: 90svw;
        height: 80vh;
        height: 80svh;
    }
}
